import './landing.css';

export default class Landing {
  constructor() {
    this.test = '';
  }

  async start() {
    this.test = '';

    const ButtonSignIn = document.getElementById('button-signin');
    function ButtonSignInClick() {
      const url = './view.html';
      document.location.href = url;
    }
    if (ButtonSignIn) {
      ButtonSignIn.removeEventListener('click', ButtonSignInClick);
      ButtonSignIn.addEventListener('click', ButtonSignInClick);
    }
  }
}
